export function initLifecycle(component) {
  component.lifecycle = {
    beforeCreate: 'beforeCreate', // 
    created: 'created', // 
    beforeRender: 'beforeRender', // 
    rendered: 'rendered', // 
    beforeUpdate: 'beforeUpdate', // 
    updated: 'updated', // 
    beforeDestroy: 'beforeDestroy', // 
    destroyed: 'destroyed' // 
  }
}
