import { isString, isObject } from '../util'

export const Global = Object.create(null) // qcharts 

Global.PLOT_INSTANCES = new WeakMap() //  Plot 
Global.THEMES = new Map() // 
Global.CURRENT_THEME = Object.create(null) // 
Global.registerTheme = (name, theme) => {
  Global.THEMES.set(name, theme)
}
Global.useTheme = theme => {
  if (!theme) {
    return
  }

  if (isString(theme)) {
    theme = Global.THEMES.get(theme)
  }

  if (isObject(theme)) {
    Global.CURRENT_THEME = Object.assign({}, Global.CURRENT_THEME, theme)
  }
}
